.filter-section {
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    padding: 20px;
    box-shadow: var(--background-shadow);
}

.filter-section .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 20px;
}

.filter-section .apply-button {
    display: flex;
    justify-content: center;
    margin-top: auto;
}